html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Poppins", sans-serif;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

p {
  font-size: 2rem;
  margin: 0;
  padding: 0;
  line-height: 2;
  color: #000000;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

h1,
h2,
h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.img {
  width: 100%;
}

.notavif.notwebp .header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/page/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}

.webp .header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/page/header.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}

.avif .header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/page/header.avif");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}

.header {
  padding: 5rem 0;
}
.header .logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header .logo {
    margin: 0;
  }
}
.header-content {
  padding: 10rem 0;
}
.header-content h1 {
  font-size: 4rem;
  color: #ffffff;
}
@media (min-width: 768px) {
  .header-content h1 {
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .header-content h1 {
    font-size: 6rem;
    max-width: 40%;
  }
}
@media (min-width: 1024px) {
  .header-content {
    padding: 15rem 0;
  }
}

@media (min-width: 768px) {
  .creator-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
.creator-content h2 {
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
.creator-content h2::after {
  content: "";
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .creator-content h2 {
    margin-top: 0;
  }
}

.benefits {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .benefits-grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 4rem;
    align-items: center;
  }
}
.benefits-content {
  grid-column: 1/4;
}
.benefits-content h2 {
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
.benefits-content h2::after {
  content: "";
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .benefits-content h2 {
    margin-top: 0;
  }
}
.benefits-image-container {
  width: 50%;
  margin: 0 auto;
  grid-column: 4/6;
  transition: transform 0.2s ease;
}
.benefits-image-container:hover {
  transform: rotate(6deg) scale(1.05);
}
@media (min-width: 768px) {
  .benefits-image-container {
    width: 100%;
  }
}

.statistics h2 {
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
.statistics h2::after {
  content: "";
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .statistics h2 {
    margin-top: 0;
  }
}

.plans {
  background-color: #313c4e;
}
.plans h2 {
  display: inline-block;
  margin-top: 5rem;
  color: #ffffff;
}
.plans h2::after {
  content: "";
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .plans h2 {
    margin-top: 0;
  }
}
.plans-description {
  color: #ffffff;
}
.plans-selection {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .plans-selection {
    max-width: 80rem;
    margin-inline: auto;
    margin-block-start: 10rem;
  }
}
@media (min-width: 1024px) {
  .plans-selection {
    gap: 0;
    flex-direction: row;
  }
}

.plan {
  flex-grow: 1;
  padding: 16rem 4rem 4rem 4rem;
  margin-bottom: 2rem;
  transition: transform 0.2s ease;
  background-color: #ffffff;
}
.plan.basic {
  background-image: url("../img/svg/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.plan.business {
  order: -1;
  background-color: #84d9bc;
  background-image: url("../img/svg/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 1024px) {
  .plan.business {
    order: unset;
    margin-block: -5rem;
    border-radius: 2rem;
    background-position: 50% 7rem;
    padding-top: 21rem;
    z-index: 10;
  }
}
.plan.business .btn-plan:hover {
  background-color: #ffffff;
}
.plan.pro {
  background-image: url("../img/svg/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.plan h3 {
  text-transform: uppercase;
}
.plan .price {
  font-size: 1.8rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.plan .list li {
  text-align: center;
  margin-bottom: 2rem;
}
.plan .list li:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .plan {
    margin-bottom: 0;
  }
  .plan:hover {
    transform: scale(1.05);
  }
}

.testimonials-image-container {
  width: 50%;
  margin: 0 auto;
}
.testimonials-content h2 {
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
.testimonials-content h2::after {
  content: "";
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .testimonials-content h2 {
    margin-top: 0;
  }
}
@media (min-width: 1024px) {
  .testimonials {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 4rem;
    align-items: center;
  }
  .testimonials-image-container {
    grid-column: 1/3;
    width: 80%;
  }
  .testimonials-content {
    grid-column: 3/6;
  }
}

.testimonial {
  padding: 2rem;
  margin-bottom: 4rem;
  border-radius: 1rem;
  position: relative;
  border: 0.5rem solid #313c4e;
  background-color: #84d9bc;
}
.testimonial p {
  padding-left: 6rem;
}
.testimonial::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../img/svg/comilla.svg);
  background-repeat: no-repeat;
  position: absolute;
}
.testimonial:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 1024px) {
  .testimonial {
    max-width: 70%;
  }
  .testimonial:nth-child(2) {
    margin-left: 10%;
  }
  .testimonial:nth-child(3) {
    margin-left: 20%;
  }
  .testimonial:nth-child(4) {
    margin-left: 30%;
  }
}

.footer {
  color: #ffffff;
  background-color: #313c4e;
}
.footer-grid {
  padding: 5rem 0;
}
.footer-image-container {
  width: 50%;
  margin: 0 auto;
}
.footer-widget {
  margin-bottom: 2rem;
}
.footer-widget:last-of-type {
  margin-bottom: 0;
}
.footer-widget h3 {
  text-align: left;
  margin-bottom: 3rem;
  color: #84d9bc;
}
.footer-nav a {
  display: block;
  margin-bottom: 1.5rem;
  transition: color 0.2s ease;
  color: #ffffff;
}
.footer-nav a:last-of-type {
  margin-bottom: 0;
}
.footer-nav a:hover {
  color: #84d9bc;
}
.footer-copyright {
  padding: 1rem;
  text-transform: uppercase;
  text-align: center;
  border-top: 2px solid #84d9bc;
  max-width: 120rem;
  margin: 0 auto;
}
.footer-copyright p {
  color: #ffffff;
}
@media (min-width: 768px) {
  .footer-image-container {
    width: 80%;
  }
  .footer-widget {
    margin-bottom: 0;
  }
  .footer-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
  }
}

.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #84d9bc;
}

.btn-primary, .btn-plan {
  font-size: 2rem;
  padding: 1.2rem 4rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  color: #313c4e;
  background-color: #84d9bc;
  cursor: pointer;
}
.btn-primary:hover, .btn-plan:hover {
  background-color: #5dcda7;
}
.btn-primary:focus-visible, .btn-plan:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .btn-primary, .btn-plan {
    width: auto;
    display: inline-block;
  }
}

.btn-plan {
  margin-top: 2rem;
  display: block;
  border: 2px solid #ffffff;
}

.link-active {
  color: #313c4e;
}

.section {
  padding: 3rem 0;
  overflow: hidden;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}